*{
  box-sizing: border-box;
}

body{
  margin: 0;
  background-color: #282d35;
}

nav{
  display: flex;
  align-items: center;
  background-color: #21222A;
  padding: 30px 25px;
  height: 90px;
}

nav >h3,nav>h4{
  margin: 0;
}

nav > h3{
  margin-right: auto;
  color: #61DAFB;
  font-weight: 700;
  font-size: 22px ;
}
nav>h4{
  color: #DEEBF8;
  font-weight: 600;
}

nav>img{
  height: 30px;
  margin-right: 7px;
}

main{
  padding: 57px 27px;
  color: white;
}

.main--title{
  margin: 0;
  font-size: 39px;
  letter-spacing: -0.05em;
}
.main--facts{
  margin-top: 46px;
  max-width: 400px;
}
.main--facts >li{
  line-height: 19px;
  padding-block: 10px;
}
.main--facts >li::marker{
  font-size: 1.4rem;
  color:#61DAFB;
}